import { TokenPayload } from './token';
import { Component } from "@angular/core";

const template = require("./app.component.html");
/* global require */

@Component({
  selector: "app-home",
  template,
})
export default class AppComponent {
  welcomeMessage = "Team";
  subject = "Yet To Read from context";
  payload:TokenPayload = {
    appctxsender: '',
    msexchuid: ''
  };
  urls;

  constructor(){}

  ngOnInit(){
    this.subject = Office.context.mailbox.item.subject;
    this.urls = Office.context.mailbox.item.getEntitiesByType(Office.MailboxEnums.EntityType.Url);
  }

  async run() {
    let subject = "";
    Office.context.mailbox.getUserIdentityTokenAsync(function(result){
      if (result.status != Office.AsyncResultStatus.Succeeded){
        console.log("error")
      }else{
        let token = result.value.split('.')
        // let headers = atob(token[0])
        // result.asyncContext.headers = JSON.parse(headers)
        let payload = JSON.parse(atob(token[1]))
        payload.appctx = JSON.parse(payload.appctx)
        result.asyncContext.payload = {
          appctxsender: payload.appctxsender,
          msexchuid: payload.appctx.msexchuid
        }
        console.log(result.asyncContext.payload)
      }
    }, this)
  }

  
}
